/*  */
/* css for image slider */
/* currently used in work page */
/* image slider's buttons are also here */
/*  */

/* container */
.container-slider {
	padding: none;
	position: relative;
	height: 100%;
}
/* slide */
.image-slide {
	width: 100%;
	height: 100%;
	position: absolute;
	opacity: 0;
	-webkit-transition: opacity ease-in-out 0.4s;
	-o-transition: opacity ease-in-out 0.4s;
	transition: opacity ease-in-out 0.4s;
}
/* active slide */
.active-image-slide {
	opacity: 1;
}
/* image */
.image-slide img {
	width: 100%;
	height: 100%;
	-o-object-fit: scale-down;
	   object-fit: scale-down;
}

/*  */
/* the buttons to change image*/
/*  */
.btn-slide {
	height: 4em;
	width: 4em;
	border-radius: 50%;
	background: var(--color);
	border: none;
	opacity: 0.25;
	position: absolute;
	display: -ms-flexbox;
	display: flex;
	-ms-flex-pack: center;
	    justify-content: center;
	-ms-flex-align: center;
	    align-items: center;
	cursor: pointer;
}
.btn-slide svg {
	width: 75%;
	opacity: 1;
	fill: var(--bg-color);
}
.prev {
	top: 50%;
	left: 20px;
	-webkit-transform: translateY(-60%);
	    -ms-transform: translateY(-60%);
	        transform: translateY(-60%);
}
.next {
	top: 50%;
	right: 20px;
	-webkit-transform: translateY(-60%);
	    -ms-transform: translateY(-60%);
	        transform: translateY(-60%);
}

/*  */
/* split screen */
/*  */
@media (max-width: 1100px) {
}

/*  */
/* mobile jazz */
/*  */
@media screen and (max-width: 600px) {
	/* btns */
	.btn-slide {
		height: 3em;
		width: 3em;
	}
}



